






















import uniLoadMore from '../../../components/uni-load-more.vue'

export default {
	data() {
		return {
			list: [],
			page: 1,
			is_load: 0,
			no_list: 0,
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onLoad() {
		this.getMessage();
	},
	onPullDownRefresh() {
		this.loadingType = 0;
		this.page = 1;
		this.list = [];
		this.is_load = 0;
		this.getMessage();
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		this.getMessage();
	},
	methods: {
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if (openid === null) {
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
			} else {
				uni.navigateTo({
					url: url
				})
			}
		
		},
		getMessage(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const page = this.page;
			try {
				this.$http.post(this.websiteUrl+'/api/message/getMessage',{openid,sessionKey,page},(res) => {
					if(res.data.code==1){
						if(res.data.result.list.length>=1){
							this.list = this.list.concat(res.data.result.list);
							this.loadingType = 0;
							this.page = this.page + 1;
							this.is_load = 1;
							this.no_list = 1;
						}else{
							this.loadingType = 2;
							if(this.page == 1){
								this.no_list = 0;
								this.is_load = 0;
							}
						}	
					}else{
						this.loadingType = 0;
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				this.loadingType = 0;
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		}
	},
	components: {
		uniLoadMore
	}
}
